import React from 'react';
import imgSobre from './Img/img_sobre.jpeg';
import './CssSobre.css';
import './Responsive.css';

function Sobre() {
    return (
      <div >
       <header class="body-sobre" > 
        <h2 class="title-sobre">Conhea nossa histria</h2>                                                                
        <nav class="nav-sobre"> 
         <img class="img-sobre" src={imgSobre} alt="Ilustrao"/>    
          <section class="section-sobre">          
             <p>Presente no mercado desde 2015, escolher por nossos produtos  preferir conforto e qualidade.
                Nossa fbrica se encontra em Campinas/SP no Distrito do Campo Grande, com atendimento em toda regio ao
                entorno de Campinas, com uma variedade de produtos como Sofs, Poltronas, Cabeceira de Cama, Ba de Cama, 
                Puffs, entre outros produtos...</p>
             <p>Tudo isso com uma equipe totalmente qualificada, juntamente com matria-prima de qualidade dando ao cliente
                um produto de alta qualidade e durabilidade que  seu novo estofado.</p>
             <p>Ns da Estofados Oliveira fazemos peas nicas e personalizveis como voc preferir para seu ambiente.</p>
             <p>A nossa empresa, pode at ser pequena, mas isso no implica na qualidade dos produtos.
                Acreditamos que  possvel oferecer um servio de alta qualidade a preos totalmente acessveis.</p>
         </section>
        </nav>
       </header>
      </div>
    );
  }
  
  export default Sobre;